.login-box{display:flex;align-items:flex-start;position:relative}.login-box svg{fill:var(--text-primary)}.hidden-item{display:none}.hidden-item-active{position:absolute;right:-80%;top:100%;padding:.5rem;border-radius:.5rem;display:flex;flex-direction:column;background-color:var(--bg-secondary)}.hidden-item-active a{color:var(--text-primary);text-decoration:none;padding:.5rem;border-radius:.5rem;transition:background-color .3s}.hidden-item-active a:hover{background-color:var(--bg-primary)}